.dashboard-grid {
  margin: var(--main-padding);
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-column-gap: 25px;
}
.dashboard-item {
  display: grid;
  grid-template-rows: 50px auto 50px;
  border-radius: 5px;
  box-shadow: var(--shadow);
  min-height: 250px;
  overflow: hidden;
  margin-bottom : 15px;
}
.
.dashboard-item-header {
  background: var(--secondary-color);
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5px 15px;
  padding: 5px var(--main-padding);
  color: white;
}
.dashboard-item-content {
  padding: 10px;
}
.dashboard-item-footer {
  padding: 10px;
}
.user-list {
  width: 100%;
  text-align: left;
  border-collapse: collapse;
}
.user-list__name , .user-list__date, .user-list__code, .user-list th {
  padding: 5px;
}
.user-list__name {
  cursor: pointer;
 
  border-radius: 5px;
    display: block;
    margin: 5px;
    box-sizing: border-box;
    background: #ffffff87;
}
.user-list__item{
  transition: all .5s;
}
.user-list__item:nth-child(even){
  background: #00000026;
}
.user-list__item:hover {
  background: #00000046;
}
.user-grid{
  background: rgba(0,0,0,0.1);
  border-radius: 5px;
}
.user__table {
  width : 100%;
}
.admin-course__grid-item {
  border-radius: 5px;
  overflow: hidden;
  box-shadow: var(--shadow);
  position : relative;
  max-height: 150px;
}
.admin-course__grid-item img {
 
  object-position: -75px;
  height: 150px;
  width: auto;
  object-fit: contain;
  filter: blur(2px);
  transition: all .5s ease-in-out;
  opacity: .7;;
}
.admin-course__grid-item:hover img {
  filter: blur(0);
  opacity: 1;
}


 .btn-selector {
  position: absolute;
  bottom: 5px;
  z-index: 2;
  max-width: 48px;
  background: #00000046;
  border: #ffffffb8 2px solid;
  box-shadow: var(--shadow);
 }
 .checkbox-custom {
  opacity: 0;
  position: absolute;  
}
.checkbox-custom:checked + .checkbox-custom-label:before {
  content: "\e834";
  font-family: 'Material Icons';

}
.checkbox-custom-label {
  position: relative;
  line-height: 1rem;
  cursor: pointer;
  color: #004475;
}
.checkbox-custom + .checkbox-custom-label:before {
  content: "\e835";
  font-family: 'Material Icons';
  font-size: 1.4rem;
  /* background: #fff; */
  display: inline-block;
  vertical-align: middle;
  width: 20px;
  height: 20px;
  margin: 5px 5px 0;
  text-align: center;
}

.checkbox-custom:checked + .checkbox-custom-label:before {
  content: "\e834";
  font-family: 'Material Icons';
  /* font-size: 2rem; */
  color: #004475;
}

.form__item .important_btn {
  background : var(--tertiairy-color);
}

.checkbox-custom:focus + .checkbox-custom-label {
outline: 1px solid #ddd; /* focus style */
}
.filter_options {
  padding: 10px;
    border-radius: 5px;
    box-shadow: var(--shadow);
    background: rgba(255, 255, 255, 0.342);
    width: 95%;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 2fr 1fr;
}
#filtered .admin-course__grid-item {
  opacity: .2;
  transition: all .5s;
}
#filtered .active, #filtered .admin-course__grid-item:hover {
  opacity: 1;
}
.filter-bar{
  border: 1px rgba(0,0,0,.04) solid;
  padding: 5px 10px;
  margin: 5px;
  background: rgba(255,255,255,0.3);
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 1rem;
  align-items: center;
}
.checkbox-filter{
  text-align: right;
  display: inline-block;
}
#filter-users {
  width: 100%;
}
 .active .btn-selector {
  background: var(--tertiairy-color);
}
.viewed__list {
  background: rgba(0,0,0,.1);
  padding: 5px;
  /* margin: 10px; */
  border-collapse: collapse;
  width: 100%;
}
.viewed__list th, .viewed__list td {
  text-align: left;
  padding: 5px;
}
.viewed__list__item:nth-child(2n) {
  background: rgba(0,0,0,.15);
}
.viewed__list__item a {
  text-decoration: none;
  color : var(--secondary-color);
}
.viewed__list__item .material-icons {
  font-size: 14px; 
  color : var(--secondary-color);
  margin-right: 5px;
}
.subscription_list_item {
  min-height: 34px;
  border-radius: 5px;
  overflow: hidden;
  box-shadow: var(--shadow);
  margin: 10px 0;
  /* overflow: hidden; */
}
.subscription_list_item li {
  margin-left: 10px;
  
}
.subscription-item-header {

  background: var(--secondary-color);
  display: grid;
  grid-template-columns: 28px 1fr 28px;
  padding: 0 5px;
  align-items: center;
  color: var(--primary-color);
  height: 34px;
}
.subscription_courses_list ol {
  list-style-type:none;
}
.subscription_courses_list{
  padding: 5px;
}
.blob {
  display: inline-grid;
    padding: 0 7px;
    background: white;
    margin-right: 10px;
    border-radius: 50%;
    color: var(--secondary-color);
}

.dashboard__item{
  display: grid;
    grid-template-rows: 50px auto;
    border-radius: 5px;
    box-shadow: var(--shadow);
    min-height: 110px;
    overflow: hidden;
    margin: 15px;
}
.dashboard__item-columns{
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-column-gap: 10px;
  margin: 10px;
}
.courses-list{
  max-height : 250px;
  overflow: auto;
  max-height: 250px;
  overflow: auto;
  padding: 5px;
  border: 2px rgba(0,0,0,0.1) solid;
}
.dashboard__description{
  margin: 5px 15px;
    background: rgba(0,0,0,0.1);
    padding: 10px;
    border-radius: 10px;
    max-height: 44px;
}
.select-css {
  display: block;
  /* font-size: 16px;
  font-family: sans-serif; */
  font-weight: 700;
  color: #444;
  line-height: 1.3;
  padding: 5px 10px;
  width: 100%;
  max-width: 100%; 
  box-sizing: border-box;
  margin: 0;
  border: 1px solid #aaa;
  box-shadow: 0 1px 0 1px rgba(0,0,0,.04);
  border-radius: 3px;
  -moz-appearance: none;
  -webkit-appearance: none;
  appearance: none;
  background-color: #fff;
  background-image: url('data:image/svg+xml;charset=US-ASCII,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%22292.4%22%20height%3D%22292.4%22%3E%3Cpath%20fill%3D%22%23007CB2%22%20d%3D%22M287%2069.4a17.6%2017.6%200%200%200-13-5.4H18.4c-5%200-9.3%201.8-12.9%205.4A17.6%2017.6%200%200%200%200%2082.2c0%205%201.8%209.3%205.4%2012.9l128%20127.9c3.6%203.6%207.8%205.4%2012.8%205.4s9.2-1.8%2012.8-5.4L287%2095c3.5-3.5%205.4-7.8%205.4-12.8%200-5-1.9-9.2-5.5-12.8z%22%2F%3E%3C%2Fsvg%3E'),
    linear-gradient(to bottom, #ffffff 0%,#e5e5e5 100%)
    ;
  background-repeat: no-repeat, repeat;
  background-position: right .7em top 50%, 0 0;
  background-size: .65em auto, 100%;
}
.select-css::-ms-expand {
  display: none;
}
.select-css:hover {
  border-color: #888;
}
.select-css:focus {
  border-color: #aaa;
  color: #222; 
  outline: none;
}
.select-css option {
  font-weight:normal;
}


