:root {
  --radius: 2px;
  --baseFg: dimgray;
  --baseBg: white;
  --accentFg: #006fc2;
  --accentBg: #bae1ff;
}
.modal{
  position: fixed;
  left: 0;
  right : 0;
  top:0;
  bottom: 0;
  background: #00000046;
  display: grid;
  justify-content: center;
  z-index: 100;
  /* align-content: center; */
}
.modal-box {  
  min-width: 800px;
  /* max-width : 1020px; */
  
  padding: 10px;
  background: #ffffff;
  max-height: 70%;
  overflow: hidden;
  border-radius: 5px;
  margin-top: 100px;
  box-shadow: var(--shadow);
  display: grid;
  grid-template-rows: 35px auto 35px;
}
.modal-header {
  background: var(--tertiairy-color);
  color: white;
  height: 48px;
  padding: 0 5px;
}
.modal-header-grid{
 display: grid;
 grid-template-columns: 32px auto 47px;
 align-items: center;

} 
.low-action-btn {
  cursor: pointer;
  width: 42px;
  background: none;
  border: none;
  box-shadow: none;
}
.modal-main-content {
  margin-top: 20px;
  /* height: calc(100% - 100px); */
  overflow: auto;
}
.modal-groups {
  min-height: 100px;
}
.modal-group-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}
.pill {
  color: white;
  background: var(--secondary-color);
  padding : 5px 20px;
  border-radius: 20px;
  margin: 5px 10px 5px 0;
  display: grid;
  grid-template-columns: 1fr auto;
  align-content: center;
}
.pill.not-active {
  opacity : 0.5;
}

.subscriptions__list {
  height: 100px;
  overflow: auto;
  margin: 5px;
}
.dashboard-item-header {

  background: var(--secondary-color);
  display: grid;
  padding: 0 5px;
  align-items: center;
  color: white;
  height: 34px;
}
.viewedlist-nav{
  display: flex;
  flex-direction: column;
  /* width: 100%; */
  justify-content: center;
  text-align: center;
  padding: 5px;
  margin: 5px;
  background: rgba(0,0,0,.1);
  border-radius: 5px;
}
.small-nav {
  font-size: .1rem;
}
.viewedlist-list{
  list-style-type: none;
}