.course-card{

    width: 100%;
    max-width: 600px;
    margin: var(--main-padding) auto;
    padding: var(--main-padding);
    box-shadow: var(--shadow);
    background-color: #eef1f3c4;
    margin-bottom: 50px;
    border-radius: 5px;
    line-height: 1.8rem;
    display: grid;
}
.course__detail_figure {
  min-height: 200px;
  max-height: 400px;
  margin: var(--main-padding) 0;
  
}
.course__detail_figure img {
  display: block;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: var(--shadow);
}
.cta-btn{
  width: 100%;
  background: var(--tertiairy-color);
  color: white;
  font-size: 120%;
  padding: var(--main-padding) !important;
  box-shadow: var(--shadow);
  transition: all .5s ease-in-out;
}
.cta-btn:hover {
  box-shadow: 0 0 25px rgba(0, 0, 0, 0.52);

}
.course__detail-meta {
  padding: 10px;
  border-top : 1px solid grey;
  border-bottom : 1px solid grey;
}
.course-item-title{
  float: left;
  margin-right: 10px;
}
.course-listing{
  list-style-type: none;
  margin: 0;
  padding: 0;
}
.course-link{
  text-decoration: none;
  background: var(--secondary-color);
  padding: 5px;
  color: white;
  border-radius: 5px;
  display: inline-block;
  margin-bottom: 5px
}