.video__viewer {
  display: flex; 
  height: calc((9/16)*100vw);
  overflow: hidden;
  position: relative;
  margin-bottom: var(--main-padding);
}
#theater {
  width: 100%;
}
  #stage {
    position: relative;
    width: 100%;
    height: 100%;
    background: black;
  }
  #stage iframe {
    position: relative;
    width: 100%;
    height: 100%;
  }
  .video__list {
    width: 410px;
    background-color: #00000093;
    height:100%;
    overflow: auto;
    position: absolute;
    z-index: 100;
    left: 0;
    transition: all .5s ease-in-out;
    /* transition-delay: .5s; */
  }
  .course-list {
    margin-bottom: var(--main-padding);
  }
  .metaData {
    z-index: 50;
    width: 400px;
    position: absolute;
    right: 0;
    background: rgba(255,255,255, 0.6);
    height: 100%;
    overflow: auto;
    transition: all 1s;
  }
  .chapter__header {
    padding: 5px 10px;
    background: var(--secondary-color);
    color: white;
    margin: 5px 10px;
    border-radius: 5px;
    display: flex;
    justify-content: space-between;
    align-content: center;
    line-height: 1.8rem;
  }
  .chapter__list-item {
    margin-left: 10px;
    white-space: nowrap;
    overflow: hidden;
  }
  .chapter__list-item__link {
    padding: 5px;
    color: rgba(255, 255, 255, 0.774);
    display: block;
    text-decoration: none;
    transition: all .5s;
  }
  .chapter__list-item__link.active{
    border: 1px solid #ffffff77;
    border-radius : 3px;
    color: white;
    background-color: #00000080;
    width: 95%;
  }
  .chapter__list-item__link:hover {
    background-color: #0000004b;
    border-radius: 5px;
    width: 95%;
    color: white;
  }
.video__footer {
  width: 95%;
  margin: auto;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-column-gap: var(--main-padding);
}
  .leftColumn {
    padding: var(--main-padding);
   border-radius: 5px;
    box-shadow : var(--shadow);
    margin-right: var(--main-padding);
    margin-bottom: 50px
  }
  .rightColumn {
    border-radius: 5px;
  padding: var(--main-padding);
  box-shadow : var(--shadow);
  margin-bottom: 50px
  }
  .right-content {
    padding: var(--main-padding);
  }
.course__meta {
  /* border-bottom: 1px dotted grey; */
    padding: 5px 15px;
    margin-bottom: var(--main-padding);
    display: grid;
    grid-template-columns: 1fr 1fr;
}
.centerAll {
  background: black;
  display: grid;
  justify-content: center;
  align-content: center;
  width: 100%;
  height: 100%;
  min-height: 500px;
}
.centerAll>div {
  width: 250px;
  height: 250px;
  padding-top: 30px;
  background-image: radial-gradient(white 5%, rgba(255,255,255,1) 15%, rgba( 255,255,255,0) 60%);
}
.hide__list {
  left: -600px;
}
.list-toggle-btn{
  position: absolute;
  top : 10px;
  left: 5px;
  z-index: 100;
  transition: all .5s ease-in-out;
  width: 34px;
    height: 34px;
    display: flex;
    align-items: center;
    
}
.list-toggle-btn .material-icons {
  transform: rotate(180deg); 
  transition: all .5s ease-in-out;
  margin-right: 0;
  position: relative;
  right: 9px;
}
.list-toggle-btn.hide__list {
  left: 410px;
}
.list-toggle-btn.hide__list .material-icons {
  transform: rotate(0);
  right: 0;
}

.nextPopup{
  position: absolute;
  top: 60px;
  right: 20px;
  background: rgba(0,0,0,0.5);
  padding: 10px;
  color: white;
  font-size: 1.2rem;
  border-radius: 5px;
}
.switch {
  margin-left: 10px;
  position: relative;
  display: inline-block;
  width: 40px;
  height: 22px;
  cursor: pointer;
}

/* Hide default HTML checkbox */
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;

  transition: .4s;
  box-shadow: inset 1px 1px 5px rgba(0,0,0,0.4);
}

.slider:before {
  position: absolute;
  content: "";
  height: 14px;
  width: 14px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  transition: .4s;
  box-shadow: 1px 1px 5px rgba(0,0,0,0.4);
}

input:checked + .slider {
  background-color:var(--secondary-color);
}

input:focus + .slider {
  /* box-shadow: 0 0 1px var(--secondary-color); */
}

input:checked + .slider:before {

  transform: translateX(18px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}
.player-bar{
  margin-bottom: 10px;
  margin-left: 20px;
}
.placeholder-video{
  min-height: 400px;
  background: black;
  display: grid;
  align-content: center;
  justify-content: center;
  color: white;
}

.course__detail-meta .progress-bar {
  width : 100%;

}
.video-panel{
  padding: 5px 10px;
    background: rgba(0,0,0,0.1);
    border-radius: 5px;
    text-align: right;
}