.groups__grid {
  display: grid;
  grid-template-columns: repeat( auto-fill , minmax(500px , 1fr));
  grid-gap: 5px;
  position: relative;

}
.group__item{
  border-radius : 5px;
  box-shadow: var(--shadow);
  /* height: 200px; */
  padding: 5px;
  display: grid;
  grid-template-rows: 35px auto;
  position: relative;
  transition: all .5s ease-out;
}
.group__description {
  /* min-height: 100px; */
  padding: 5px;
}
i{
  user-select: none;
}
.group__item h3 {
  width: 100%;
  height: 30px;
  overflow: hidden;
}
.group__item .group__footer {
background-color: #2d89cc14;

}
.circle{
  position: absolute;
  right: 50px;
  top: 2px;
  background: white;
  padding: 0 2px;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  color: var(--secondary-color);
  display: grid;
  align-content: center;
  justify-content: center;
  font-weight: 700;
}
.invi-select {
  opacity : 0;
  position: absolute;  
}
.group__label {
  cursor: pointer;
  position: relative;
  opacity: 0.7;
}
.group__label:hover{
  opacity: 1;
}
.group__label::after{
  font-family: 'Material Icons';
    content: '\E5CF';
    position: absolute;
    top: 5px;
    right: 10px;
    font-size: 24px;
    color: white;

}
.group_table{
  height:0;
  overflow: hidden;
  background: rgba(0,0,0,0.1);
  opacity: 0;
  /* transition: all .5s ease-out; */
  position: relative;
  
}

input.invi-select:checked ~ .group_table {
  border-radius: 5px;
  box-shadow: var(--shadow);
  padding: 5px 5px 15px;
  margin: 0 5px 15px;
  height: 100%;
  opacity: 1;
}
input.invi-select:checked ~ .group__label::after {
 content: '\e5ce';
}
/* .dashboard__item-columns {
  height: 0;
  overflow: hidden;
} */
input.invi-select:checked ~ .dashboard__item-columns {
  height: 100%;
}


.user-list__item:nth-child(even) {
  background: #00000026;
}
.user-list th {
  cursor: pointer;
  background: #00000000;
  transition: all .5s ;
}
.user-list th:hover {
 background : #00000022;
}
.user-list__icon .state {
  display: block;
  margin: 0 auto;
  float: none;
}
.user-list__code{
  font-weight: 700;
}
.state {
  height: 18px;
  width: 18px;
  display: inline-block;
  border-radius: 50%;
  float: right;
}
.state_0{
  background: orange;
}
.state_1{
  background: green;
}
.state_2{
  background: red;
}