.dashboard {
  box-shadow : var(--shadow);
  min-height : 50vh;
  border-radius : 5px;
  overflow: hidden;
  display: grid;
    grid-template-rows: auto 1fr auto;
}
.dashboard__content{
  margin: var(--main-padding) 5px;
  display: grid;
  grid-column-gap: var(--main-padding);
  grid-template-columns: 1fr 1fr;
  height: 100%;
}
.dashboard-item-content .important_btn{
  width: 97%;
  color: white;
}
.dashboard__content-panel {
  width: 100%;
}
.dashboard-footer {
  background: rgba(200, 200, 200, 0.144);
  padding: 10px;
}
.dashboard__grid-item {
  position: relative;
  box-shadow: var(--shadow);
  overflow: hidden;
  height: 155px;
  border-radius: 3px;
}
.dashboard__course-link .dashboard__grid-item {
 transition: all 0.5s ease-in-out;
}
.dashboard__course-link:hover .dashboard__grid-item {
  box-shadow: 0 0 5px rgba(0,0,0,0.5);
  transform: scale(1.02)
}
.dashboard__link a {
  padding: 5px;
  text-decoration: none;
  color: var(--secondary-color);
  display: grid;
  grid-template-columns: 30px 3fr 1fr;
  font-size: 1.1rem;
  margin: 5px ;
  border-radius: 5px;
  box-shadow: var(--shadow);
  transition: .5s all ease-in-out;
  position: relative;
}
.align-right{
  float: right;
}
.dashboard__link a:hover {
  background-color: rgba(0,0,0,0.05);
}
.dashboard__link a:hover .tooltip{
  display: block;
  position: absolute;
  bottom: -36px;
  right: 10px;
  z-index: 21;
  background: rgba(0,0,0,0.3);
  color: white;
  padding: 5px 10px;
  border-radius: 5px;
  font-size: small;
  text-shadow: 1px 1px 2px black;
}
.dashboard__link .material-icons { 
  font-size: 0.8rem; 
  color: var(--secondary-color);
}
.dashboard__link a .course_title {
  font-size: 35%;
 
}
.dashboard__link-title {
  display: block;
  overflow: hidden;
  white-space: nowrap;
  width: 95%;
}
.tooltip{
  display: none;
}
.icon{
  height: 26px;
  padding: 5px;
  background: white;
  border-radius: 50%;
  /* transform: translateY(7px); */
}
.course__list_view{
  min-height: 250px;
  max-height: 500px;
  overflow: auto;
  margin: 10px 0;
  padding-bottom: 35px;
  position: relative;
}
.thumb-header {
  position: absolute;
  top: 0;
  padding: 10px;
  background: #00000046;
  color: white;
  width: 100%;
  z-index: 1;
}
.thumb-header h5 {
  font-weight: 400;
}
.thumb-btn {
  position: absolute;
  bottom: 5px;
  right: 5px;
  background: #00000046;
  border: #ffffffb8 2px solid;
  box-shadow: var(--shadow);
}